				hat: false,
				hat1: true,
				packageId: '',
				active1: '',
				packageName: '包月默认套餐', //套餐名
				monthNum: 10, //包月次数/时长（小时）
				comeStandard: 0, //加油包金额
				comeNum: 0, //加油包次数/时长
				remarks: '', //备注
				// type: '',
				hat: false,
				hat1: false,
				numtitle: 1, //允许最大购买次数
				radio: '1', //计次选择 按天/30天
				addshow: false, //计次档位弹窗显示
