				twovul: '', //计次弹窗第二个值
				moneyvul: '', //计次弹窗金额
				twolist: [{
					left: 0,
					right: 200,
					money: 30
				}, {
					left: 201,
					right: 420,
					money: 45
				}, {
					left: 421,
					right: 800,
					money: 60
				}],
				smoney: 801,
				twohat: 120,
				checked: true,
