			//获取一个小区的费率套餐列表
			rate() {
				if (this.type == 3) {
					uni.showLoading({
						title: '加载中',
					})


					this.$base.request1('manager/parkingPackageMsg' + '/' + this.projectId, 'GET')
						.then(res => {
							uni.hideLoading()
							if (res.data.code == 200) {
								let arr = res.data.data
								let toption1 = [{
									text: '1期',
									value: 1
								}, {
									text: '2期',
									value: 2
								}, {
									text: '3期',
									value: 3
								}, {
									text: '4期',
									value: 4
								}, {
									text: '5期',
									value: 5
								}, {
									text: '6期',
									value: 6
								}, {
									text: '7期',
									value: 7
								}, {
									text: '8期',
									value: 8
								}, {
									text: '9期',
									value: 9
								}, {
									text: '10期',
									value: 10
								}, {
									text: '11期',
									value: 11
								}, {
									text: '12期',
									value: 12
								}]
								for (var i = 0; i < arr.length; i++) {
									arr[i].steps = JSON.parse(arr[i].disMsg)
									if (arr[i].statuss == 0) {
										arr[i].statusstype = false
									} else {
										arr[i].statusstype = true
									}
									let qishu = []
									for (var j = 0; j < arr[i].steps.length; j++) {
										qishu.push(arr[i].steps[j].nums)
									}
									for (var j = 0; j < arr[i].steps.length; j++) {
										arr[i].steps[j].tvalue = arr[i].steps[j].nums
										let toption = [{
											text: '1期',
											value: 1
										}, {
											text: '2期',
											value: 2
										}, {
											text: '3期',
											value: 3
										}, {
											text: '4期',
											value: 4
										}, {
											text: '5期',
											value: 5
										}, {
											text: '6期',
											value: 6
										}, {
											text: '7期',
											value: 7
										}, {
											text: '8期',
											value: 8
										}, {
											text: '9期',
											value: 9
										}, {
											text: '10期',
											value: 10
										}, {
											text: '11期',
											value: 11
										}, {
											text: '12期',
											value: 12
										}]
										let m = 1
										for (var t = 0; t < qishu.length; t++) {
											if (t === j) {
												m--
												continue
											}
											toption.splice(qishu[t] - (m + t), 1)
										}
										arr[i].steps[j].toption = toption
									}
								}
								this.tclist = arr
							} else {
								this.$toast.fail(res.data.msg)
							}

						})
						.catch(err => {

						})
				} else {
					uni.showLoading({
						title: '加载中',
					})

					let data = {
						packageName: this.name,
						type: this.type
					}

					this.$base.request1('manager/rate/list' + '/' + this.projectId, 'GET', data)
						.then(res => {
							uni.hideLoading()
							this.addNew = res.data.data.addNew
							this.occNum = res.data.data.occNum
							if (this.occNum > 0) {
								this.radio = '2'
								this.hat = false
								this.hat1 = true
							}
							if (this.type == 0) {
								this.cylist = res.data.data.rows
							} else if (this.type == 1) {
								this.bylist = res.data.data.rows
							} else if (this.type == 2) {
								this.lslist = res.data.data.rows
							}
							if (this.addNew == 1) {
								if (this.bylist.length == 0) {
									this.checked = false
								} else {
									if (this.bylist[0].binds == 0) {
										this.checked = false
									} else if (this.bylist[0].binds == 1) {
										this.checked = true
									}
									var item = this.bylist[0]
									this.newtype = item.types
									if (item.types == 'A5') {
										this.active1 = 0
										this.radio = '1'
										this.hat = false
										this.hat1 = true
									} else if (item.types == 'A4') {
										this.active1 = 0
										this.radio = '2'
										this.hat = false
										this.hat1 = true
									} else if (item.types == 'A6') {
										this.active1 = 1
										this.radio = '2'
										this.hat = true
										this.hat1 = false
									} else if (item.types == 'A7') {
										this.active1 = 1
										this.radio = '1'
										this.hat = true
										this.hat1 = false
									}
									this.packageName = item.packageName
									this.remarks = item.remarks
									this.comeNum = item.comeNum
									if (this.occNum > 0) {
										this.occupyMax = item.occupyMax
									} else {
										this.occupyMax = 0
									}
									this.occupyRate = item.occupyRate
									this.occupyFree = item.occupyFree
									this.comeStandard = item.comeStandard
									this.monthNum = item.monthNum
									this.numtitle = item.limitNum
									this.twohat = item.steps[item.steps.length - 1].standard
									this.smoney = Number(item.steps[item.steps.length - 1].maxPower)
									this.twolist = []
									this.$delete(item.steps, item.steps.length - 1)
									item.steps.map((item1, index, array) => {
										this.twolist.push({
											'left': Number(item1.minPower),
											'right': Number(item1.maxPower),
											'money': Number(item1.standard)
										})
									})


								}
							}
						})
						.catch(err => {

						})
				}

