							this.$toast.fail('套餐名不能为空')
							return;
						}
						if (this.active1 == 1) {
							if (this.radio == '1') {
								t = 'A7'
							} else if (this.radio == '2') {
								t = 'A6'
							}
						} else if (this.active1 == 0) {
							if (this.radio == '1') {
								t = 'A5'
							} else if (this.radio == '2') {
								t = 'A4'
							}
						}
						if (this.twolist.length > 0) {
							this.twolist.map((item, index, array) => {
