							map1['maxPower'] = this.smoney
							map1['standard'] = Number(this.twohat)
							arr.push(map1)
						}
						if (!this.twohat.toString()) {
							this.$toast.fail('套餐金额不能为空')
							return;
						}

						if (this.radio == '1') {
							data = {
								packageName: this.packageName,
