			newbtn(evt) {
				if (this.active1 == 0) {
					if (this.occNum > 0 && evt == 1) {
						this.radio = '2'
						this.hat = false
						this.hat1 = true
						this.$toast.fail('当前充电区已有充电柜，不允许切换类型')
						return
					}
					if (evt == 1) {
						if (this.newtype == 'A5') {
							this.monthNum = this.bylist[0].monthNum
						} else {
							this.monthNum = 10
						}
					} else {
						if (this.newtype == 'A4') {
							this.monthNum = this.bylist[0].monthNum
						} else {
							this.monthNum = 300
						}
					}

				}

			},

			newbtn1(evt) {
				if (this.active1 == 1) {
					if (evt == 1) {
						if (this.newtype == 'A7') {
							this.monthNum = this.bylist[0].monthNum
						} else {
							this.monthNum = 1
						}
					} else {
						if (this.newtype == 'A6') {
							this.monthNum = this.bylist[0].monthNum
						} else {
							this.monthNum = 30
						}
					}
				}

