			//关闭新增档位弹窗
			addshowclose() {
				this.onevul = '',
					this.twovul = '',
					this.moneyvul = ''
				this.addshow = false
			},
			section(num) {
				if (num != 0) {
					if (num < 0 || num > 300) {
						return true
					} else {
						return false
					}
				} else {
					return false
				}

			},
			//新增
			addbtn() {
				if (!this.onevul || !this.twovul || !this.moneyvul) {
					this.$toast.fail('请完善信息')
					return
				}
				if (this.onevul > this.twovul || this.onevul == this.twovul) {
					this.$toast.fail('输入档位不能小于或等于' + this.onevul + '瓦')
					return
				}
				if (this.moneyvul == 0) {
					this.$toast.fail('金额不能为0')
					return
				}
				if (this.section(this.moneyvul) == true) {
					Dialog.confirm({
						title: '提示',
						message: '系统检测到有非正常范围内（正常为0-300元）的费率金额，点击“取消”进行检查，点击“确认”继续操作！'
					}).then(() => {
						var arr = {
							left: this.onevul,
							right: this.twovul,
							money: this.moneyvul
						}
						this.twolist.push(arr)
						this.smoney = Number(this.twolist[this.twolist.length - 1].right) + 1
						this.addshowclose();
					}).catch(() => {
						return
					});
				} else {
					var arr = {
						left: this.onevul,
						right: this.twovul,
						money: this.moneyvul
					}
					this.twolist.push(arr)
					this.smoney = Number(this.twolist[this.twolist.length - 1].right) + 1
					this.addshowclose();
				}

